const EmptyCart = () => (
  <EmptyContent>
    <EmptyTextContainer>
      <EmptyText>Your Cart is Empty</EmptyText>
    </EmptyTextContainer>
    <ShopProductsContainer>
      <ShopProducts href='/'>Shop our Products</ShopProducts>
    </ShopProductsContainer>
  </EmptyContent>
)

const Cart = ({ data }) => {
  const activeStore = useSelector(({ storeSubdomain }) => storeSubdomain)
  const { addDiscountToCheckout } = useDiscount()
  const loadReferralCouponCode = loadState('referralCouponCode')
  const loadFirstPurchaseCouponCode = loadState('firstPurchaseCouponCode')
  const [cartClickLoader, setCartClickLoader] = useState(false)
  const [discountApplicable, setDiscountApplicable] = useState(false)

  const {
    lineItems: { edges: cartLineItems },
    subtotalPriceV2,
    lineItemsSubtotalPrice,
    webUrl,
  } = useSelector(({ checkouts }) => checkouts[activeStore])

  useEffect(() => {
    if (!subtotalPriceV2 || !lineItemsSubtotalPrice) return

    if (
      parseFloat(lineItemsSubtotalPrice.amount) -
        parseFloat(subtotalPriceV2.amount) >
      0
    ) {
      setDiscountApplicable(true)
    } else {
      setDiscountApplicable(false)
    }
  }, [subtotalPriceV2, lineItemsSubtotalPrice])

  const captureDiscountApplicationError = (code, message) => {
    captureWarning(
      `Discount code generated could not be applied : ${code} - ${message}`,
    )
  }

  const getCouponCode = () => {
    if (loadReferralCouponCode) {
      return loadReferralCouponCode.code
    }
    if (loadFirstPurchaseCouponCode) {
      return loadFirstPurchaseCouponCode.code
    }
    return null
  }

  const redirectToCheckout = async checkoutURL => {
    checkoutURL = checkoutURL.replace(
      'https://lastbrand.myshopify.com',
      process.env.GATSBY_CHECKOUT_URL,
    )

    try {
      const discountCode = getCouponCode()
      if (discountCode) {
        const windowGlobal = typeof window !== 'undefined' && window
        const checkoutId = windowGlobal.localStorage.getItem(
          getLocalStorageCheckoutKey(activeStore),
        )

        addDiscountToCheckout(discountCode, checkoutId).then(
          ({ response, error, loading }) => {
            if (response) {
              const { data } = response
              const errors = data.checkoutDiscountCodeApplyV2.checkoutUserErrors

              if (errors.length > 0) {
                captureDiscountApplicationError(discountCode, errors[0].message)
                const errorCodes = errors.map(e => e.code)
                if (errorCodes.indexOf('DISCOUNT_LIMIT_REACHED') > -1) {
                  deleteState('referralCouponCode')
                  deleteState('firstPurchaseCouponCode')
                }
              }
            } else if (error) {
              captureDiscountApplicationError(discountCode, error)
            }
          },
        )
      }
    } catch (error) {
      captureWarning(`Failed to Process Referral discount code during checkout`)
    }

    const loginURL = await getShopifyLoginURL(checkoutURL)
    if (loginURL) {
      setTimeout(() => setCartClickLoader(false), 2000)
      window.location.href = loginURL
    } else {
      setTimeout(() => setCartClickLoader(false), 2000)
      window.location.href = checkoutURL
    }
  }

  return (
    <>
      <PageTitle>Cart</PageTitle>
      {webUrl === null ? (
        <SpinningContainer>
          <SpinningLoader className='custom-loader' />
        </SpinningContainer>
      ) : (
        (!cartLineItems || cartLineItems.length === 0) && <EmptyCart />
      )}
      {cartLineItems && cartLineItems.length > 0 && (
        <Content>
          <Header>
            <HeaderTitle>Product</HeaderTitle>
            <HeaderTitle>Total</HeaderTitle>
          </Header>
          <LineItemsContainer>
            <LineItems lineItems={cartLineItems} />
          </LineItemsContainer>
          <Footer>
            <Total>
              <FooterTitle> Total </FooterTitle>
              <PriceContainer>
                {lineItemsSubtotalPrice && discountApplicable && (
                  <ActualPrice>
                    {`$${parseFloat(lineItemsSubtotalPrice.amount).toFixed(2)}`}
                  </ActualPrice>
                )}
                <DiscountedPrice>
                  {subtotalPriceV2 &&
                    `$${parseFloat(subtotalPriceV2.amount).toFixed(2)}`}
                </DiscountedPrice>
                {lineItemsSubtotalPrice && discountApplicable && (
                  <DiscountAmount>
                    &nbsp;(-$
                    {parseFloat(
                      parseFloat(lineItemsSubtotalPrice.amount) -
                        parseFloat(subtotalPriceV2.amount),
                    ).toFixed(2)}
                    )
                  </DiscountAmount>
                )}
              </PriceContainer>
            </Total>
            <ShippingInfo>
              <ShippingTitle>
                Shipping and taxes calculated at checkout
              </ShippingTitle>
            </ShippingInfo>
            <CheckoutContainer>
              <Checkout
                onClick={() => {
                  setCartClickLoader(true)
                  redirectToCheckout(webUrl)
                }}
                href={webUrl}
                trackParams={{
                  trackGA: true,
                  trackInternal: true,
                  category: 'checkout',
                  action: 'start_checkout',
                }}
                loading={cartClickLoader}
              >
                Checkout
              </Checkout>
            </CheckoutContainer>
          </Footer>
        </Content>
      )}
    </>
  )
}

export default Cart
